@import "../../config/colors/colors.scss";

.container {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-self: center;
  align-self: center;
  width: 50%;
  margin: 50px auto;
  padding: 10px;
  color: $colorBlue;
  border: 2px solid $colorBlue;
  border-radius: 15px;
  .blobOne{
    opacity: .5;
    position: fixed;
    top: 0;
    left: 0;
    width: 600px;
    animation: traslateOne 8s infinite;
    z-index: -1;
  }
  .blobTwo{
    opacity: .5;
    position: fixed;
    bottom: 0;
    right: 0;
    width: 400px;
    animation: traslateTwo 5s infinite;
    z-index: -1;
}

  h1 {
    padding: 0 50px;
  }

  .containerItems {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;

    div {
      margin: 10px;
    }
  }

  .containerStates {
    display: flex;
    flex-direction: column;
  }

  .btn {
    position: absolute;
    bottom: 0;
    text-decoration: none;
    align-self: flex-end;
    font-size: 30px;
    border: 2px solid $colorOrange;
    border-radius: 50%;
    padding: 10px;
    margin: 30px;
    color: $colorOrange;
    font-weight: bold;
  }

  .btn:hover {
    background-color: $colorOrange;
    color: $colorWhite;
  }
}


@keyframes traslateOne {
  0% {
      transform: translateY(0);
  }
  50% {
      transform: translateY(40%);
  }
  100% {
      transform: translateY(0);
  }      
}
@keyframes traslateTwo {
  0% {
      transform: translateY(0);
  }
  50% {
      transform: translateY(-60%);
  }
  100% {
      transform: translateY(0);
  }   
}

@media screen and (max-width: 600px) {
  .container {
    width: 90%;
    margin: 50px auto;
    .containerItems{
      flex-direction: column;
    }
    .btn{
      position: relative;
    }
  } 
}